.mb-40 {
    margin-bottom: 40px;
}

.card-st {
    width: 250px;
    display: inline-block;
    margin-right:20px;
}
.inner-card {
    border-radius: 9px;
}

.outer-div {
    overflow-x: auto;
    overflow-y: hidden; 
    margin-left:20px;
}

.inner-div {
    width: max-content;
}
.outer-div::-webkit-scrollbar {
    width: 5px;
    height: 13px;
}

/* Track */
.outer-div::-webkit-scrollbar-track {
    /* box-shadow: inset 0 0 3px grey; 
    border-radius: 4px; */
    background: white;
    border: 1px solid black;
    border-radius: 9px;
}

/* Handle */
.outer-div::-webkit-scrollbar-thumb {
    background: black; 
    border-radius: 9px;
    width: 30px;
}

/* Handle on hover */
.outer-div::-webkit-scrollbar-thumb:hover {
    background: #979698; 
}

.card-border {
    border: 1px solid red;
    border-radius: 9px;
}

.x-col-btn {
    position: absolute;
    right: 50px;
    top: 5px;
    color: orange;
    cursor: pointer;
    font-size: 40px;
    z-index: 999;
}