p {
    font-family: 'Roboto', sans-serif;
}

input[type="text"] {
    padding-left: 10px;
    height: 43px;
    color: #212529;
    width: 100%;
}

.drive-container {
    max-width: 1234px;
}

.test {
    border-radius: 0%;
    background-color: #D8D8D8;
    border-top: none;
    border-bottom: none;
    border-left: none;
    border-right-color: #979797;
    color: #9B9B9B;
}

.test:last-child {
    border-radius: 0%;
    background-color: #D8D8D8;
    border-top: none;
    border-bottom: none;
    border-left: none;
}

.standard-title p {
        display: inline;
        font-size: 14px;
}

.btn-plus{
    border: none;
    background-color: transparent;
}

.fa-plus {
    color: #6C6C6C;
    width: 81px;
    height: 81px;
    align-items: center;
    display: flex;
    justify-content: space-around;
}

div.dd-wrapper {
    font-size: 18px;
    width: 100%;
}
div.dd-wrapper div.dd-header {
    border-radius: 5px ;
    border: 1px solid #979797;
}

div.dd-wrapper ul.dd-list {
    padding: 5px;
    font-weight: 300;
}
ul.dd-list {
    position: static !important;
}
div.dd-wrapper div.dd-header-title {
    margin-left: 10px;
}
div.dd-wrapper li.dd-list-item {
    font-size: 18px;
    border-bottom: 1px solid #ddd;
}
div.dd-wrapper li.dd-list-item:hover {
    background-color: rgb(184, 207, 225);
    color: black;
}
.square{
    width: 220px; 
    height: 142px; 
    background-color: #E9E9E9; 
    border-radius: 5px;
    align-items: center;
    justify-content: space-around;
    display: flex;
    float: none;
}

.thumbnail_image {
    margin: 0 auto;
    display: block;
    border-radius: 5px;
    border: 1px solid grey;
}
.thumbnail_text {
    margin-top: 25px;
    text-align: center;
}
.spinner-padding {
    padding-left: 50%;
    padding-top: 20%;
}
.tooltip-icon {
    float: right;
    background-color: rgb(238, 238, 238);
    border-radius: 50%;
}

.__react_component_tooltip.type-dark {
    background-color: rgb(238, 238, 238);
    color: black;
}

.fixed-tooltip {
    width: 200px;
    height: 150px;
    text-overflow: ellipsis;
}

.thumbnail-wrapper {
    width: 320px;
    height: 450px;
    overflow: hidden;
    margin: 0 auto;
    display: block;
}

.w-70 {
    max-width: 90%;
}
