p, button {
    font-family: 'Roboto', sans-serif;
}

p.title {
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 24px;
    color: #4A4A4A;
}

p.text-resource{
    font-size: 18px;
    color: #4A4A4A;
}

p.text-collections {
    font-size: 20px;
    color: #4A4A4A;
}

.btn-plus{
    border: none;
    background-color: transparent;
}

.fa-plus {
    color: #6C6C6C;
    width: 81px;
    height: 81px;
}

.square {
    width: 335px; 
    height: 200px; 
    background-color: #E9E9E9; 
    border-radius: 9px 9px 0 0;
    align-items: center;
    justify-content: space-around;
    display: flex;
    float: none;
}

.round-btn {
    border-radius: 7px;
    border: 1px solid #979797;
    background: linear-gradient(#f2f2f2, #D4D4D4);
    height: 21px;
    font-size: 14px;
    color: #4A4A4A;
    font-family: 'Roboto', sans-serif;
    text-align: center;
    vertical-align: middle;
    line-height: 1em;
}

.drive-container {
    max-width: 1200px;
}

.new-collection {
    padding-top: 10px;
    width: 335px; 
    height: 60.41px; 
    background-color: #D8D8D8; 
    border-radius: 0 0 9px 9px;
}

.btn-edit-image {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    width: 40%;
    font-size: 16px;
    padding: 10px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    z-index: 999;
}

