p, button {
    font-family: 'Roboto', sans-serif;
}

p.title {
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 24px;
    color: #4A4A4A;
}

p.text-resource{
    font-size: 18px;
    color: #4A4A4A;
}

p.text-collections {
    font-size: 20px;
    color: #4A4A4A;
}

.btn-plus{
    border: none;
    background-color: transparent;
}

.fa-plus {
    color: #6C6C6C;
    width: 81px;
    height: 81px;
}

.square {
    width: 335px; 
    height: 200px; 
    background-color: #E9E9E9; 
    border-radius: 9px 9px 0 0;
    align-items: center;
    justify-content: space-around;
    display: flex;
    float: none;
}

.round-btn {
    border-radius: 7px;
    border: 1px solid #979797;
    background: linear-gradient(#f2f2f2, #D4D4D4);
    height: 21px;
    font-size: 14px;
    color: #4A4A4A;
    font-family: 'Roboto', sans-serif;
    text-align: center;
    vertical-align: middle;
    line-height: 1em;
}


