.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  }
  
  .modal-main {
    position:fixed;
    background: white;
    width: 40%;
    height: auto;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
  }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }

.inline-btn {
    margin: 15px;
} 
.x-btn {
    position: absolute;
    right: 10px;
    cursor: pointer;
    font-size: 30px;
}
h3{
    margin-top:10px;
}


  