    -webkit-backface-visibility: hidden; 
    background-color: #D8D8D8;
    border-radius: 9px;
    border: 1px solid #D8D8D8;
}
div.top-col-image:after {
    content:'';
    position:absolute;
    width:100%; height:100%;
    top:0; left:0;
    background:rgba(0,0,0,0.6);
    opacity:0;
    border-radius:9px;
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
}
div.top-col-image:hover:after {
    opacity:1;
    padding: 0 15px;
}
