html,
body {
    font-family: 'Montserrat', sans-serif;
    /*font-family: 'Roboto', sans-serif;*/
    margin: 0;
    padding: 0;
    width: 100%;
    scroll-behavior: smooth;
}

.co-teach__logo {
    width: 180px;
  }

.navbar-light .navbar-nav .nav-link {
    color: #000;
}

li.nav-item.mx-3 {
    font-size: 24px;
    font-weight: 900;
}

.smooth-hover {
    display: inline-block;
    color: #000;
    text-decoration: none;
}

.smooth-hover::after {
    content: '';
    display: block;
    width: 0;
    height: 3px;
    background: #000;
    transition: width .3s;
}

.smooth-hover:hover::after {
    width: 100%;
}

.input-group {
    margin-bottom: 15px !important;
}
div.title-bar p {
    margin-bottom: 0px !important;
}
